.tabs {
padding-bottom: 0;
}
.tabs:after {
	content: '';
	display: table;
	clear: both;
}
.tabs input[type=radio] {
	/*margin-left: $pica*2;*/
    margin-top: -0.5rem;
    /*display: inline-block;*/
}
.tabs label,
.transfer label {
	cursor: pointer;
}
.steps .transfer label {
