	/*border-top: 1px solid $color-border-grey;*/
	/*margin: $pica 0 0;*/
}

input.toggle-tab,
#details1 {
	display: none;
}

/*.tabs [id^="tab"]:checked + label {}*/


/*#tab1:checked ~ #tab-content1,
#tab2:checked ~ #tab-content2,
#tab3:checked ~ #tab-content3,*/
#details1:checked ~ #transfer-content {
	display: block;
}

$tab-number: 4;

@while $tab-number > 0 {
  #tab#{$tab-number}:checked ~ #tab-content#{$tab-number} {
    display: block;
  $tab-number: $tab-number - 1
  }
}

